import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    meta: {
      title: "首頁"
    },
    component: Home
  },
  {
    path: "/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    meta: {
      title: "關於"
    },
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue")
  },
  {
    path: "/signs",
    name: "Signs",
    meta: {
      title: "操作符"
    },
    component: () => import("../views/Signs.vue")
  },
  {
    path: "/datatype",
    name: "DataType",
    meta: {
      title: "資料型態"
    },
    component: () => import("../views/DataType.vue")
  },
  {
    path: "/conditional",
    name: "Conditional",
    meta: {
      title: "判斷式"
    },
    component: () => import("../views/Conditional.vue")
  },
  {
    path: "/datastructure",
    name: "DataStructure",
    meta: {
      title: "資料結構"
    },
    component: () => import("../views/DataStructure.vue")
  },
  {
    path: "/loop",
    name: "Loop",
    meta: {
      title: "迴圈"
    },
    component: () => import("../views/Loop.vue")
  },
  {
    path: "/intrinsicsfunction",
    name: "IntrinsicsFunction",
    meta: {
      title: "內建函式"
    },
    component: () => import("../views/IntrinsicsFunction.vue")
  },
  {
    path: "/function",
    name: "Function",
    meta: {
      title: "函式"
    },
    component: () => import("../views/Function.vue")
  },
  {
    path: "/algorithm",
    name: "Algorithm",
    meta: {
      title: "演算法"
    },
    component: () => import("../views/Algorithm.vue")
  }
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes
});

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = "程式拒絕死當 - " + to.meta.title;
  }
  next();
});

export default router;
